.enviado{
    display: flex;
        justify-content: center;
    font-size: 30px;
    padding: 5% 0 31% 0;
    text-align: center;
    background-image: linear-gradient(150deg,#9500ff,#09f,#0df);
}

.mensaje{
    padding: 1%;
    border-radius: 8px;
    color: lightgreen;
    width: 60%;
    background-color: rgba(0, 0, 0, 0.7);
}

.mensaje-error{
    padding: 1%;
    border-radius: 8px;
    color: red;
    width: 60%;
    background-color: rgba(0, 0, 0, 0.7);
}

@media only screen and (min-width:0px) and (max-width:767px){
    .enviado{
        font-size: 20px;
        padding: 5% 0 100% 0;
    }
  }