h1{
  text-align: center;
  margin-bottom: 3%;
}

.background-gradient{
  padding: 2% 0 5% 0;
  background-image: linear-gradient(150deg,#9500ff,#09f,#0df);
  display: flex;
    flex-direction: column;
    justify-content: center;
}

@media only screen and (min-width:0px) and (max-width:920px){
  .background-gradient{
    padding: 2% 0 30% 0;
  }

  .background-gradient h1{
    padding: 0 0 20% 0;
  }
}


html, body{
  font-family: 'Roboto Mono', monospace;
}











*{
  margin: 0;
  padding: 0;
}