.trabajo-tag{
    margin: 2% 6%;
    border-radius: 8px;
    border: solid black;
    color: white;
    transform-style: preserve-3d;
    cursor: pointer;
    transition: 1s;
}

.trabajo-tag:hover .header{
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    transform: rotatey(180deg);
}

.trabajo-tag:hover .content{
    transform: rotatey(0deg);
}

.header{
    font-size: 20px;
    text-align: center;
    height: 250px;
    margin: 1%;
    padding: 1%;
    border-radius: 8px;
    background-color: rgba(128, 0, 128);
    transition: 1s;
}

.content{
    top: 0;
    height: 94%;
    width: 98%;
    position: absolute;
    margin: 1%;
    padding: 5%;
    border-radius: 8px;
    background-color: rgba(128, 0, 128);
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    transform: rotatey(180deg);
    transition: 1s;
}

@media only screen and (min-width:0px) and (max-width:767px){
    
    .trabajo-tag{
        font-size: 12px;
    }

    .header{
        padding: 5% 0;
        height: 180px;
    }
    .content, .header{
        font-size: inherit;
    }

    .click{

        font-size: 15px;
    }
}