.texto-background{
    padding: 1%;
    background-color: rgba(128, 0, 128, 0.6);
    width: 50%;
    margin-top: 1%;
    border-radius: 8px;
    border: solid black;
    color: white;
    display: flex;
    justify-content: center;
    transition: .7s;
}



.texto-background:hover{
    background-color: rgba(128, 0, 128, 1);
    margin: 3% 2% 2% 2%;
    transform:scale(1.1)
}

.educacion-img{
    border-radius: 8px;
    width: 48%;
    cursor: pointer;
    transition: .6s;
}

.card-text{
    border-radius: 8px;
    padding: 1%;
    background-color: rgba(0, 0, 0, 0.7);
    width: 48%;
}

.educacion-img:active{
    transform: scale(1.1);
}

@media only screen and (min-width:0px) and (max-width:480px){

    .educacion-texto, .educacion-description, .educacion-date{
        margin: 0;
    }

    .texto-background{
       width: 90%;
       flex-direction: column;
       align-items: center;
    }

    .educacion-img{
        width: 90%;
        margin-bottom: 1%;
        cursor: pointer;
        transition: .6s;
    }
    
    .card-text{
        margin-bottom: 1%;
        font-size: 12px;
        width: 90%;
    }



    .texto-background:hover{
        background-color: rgba(128, 0, 128, 1);
        margin: 4% 3% 3% 3%;
        transform:scale(1)
    }

    .educacion-img:active{
        transform: scale(1.1);
    }
}

@media only screen and (min-width:481px) and (max-width:767px){

    
    .texto-background{
        width: 90%;
        flex-direction: column;
        align-items: center;
     }

     .educacion-img{
        width: 90%;
        margin-bottom: 2%;
        cursor: pointer;
        transition: .6s;
    }
    
    .card-text{
        margin-bottom: 2%;
        width: 90%;
    }

    .texto-background:hover{
        transform:scale(1)
    }


    .educacion-img:active{
        transform: scale(1.1);
    }
}

@media only screen and (min-width:768px) and (max-width:1200px){

    .card-text{
        margin: 1%;
        align-self: center;
        width: 90%;
    }
    
    .educacion-img{
        margin: 1%;
    }
    
    .texto-background{
        width: 90%;
    }
}

@media only screen and (min-width:1201px){

    .card-text{
        margin: 1%;
        align-self: center;
        width: 90%;
    }
    
    .educacion-img{
        margin: 1%;
    }

    .texto-background{
        width: 60%;
    }
}
