
#formulario{
    width: 60%;
    padding: 3% 0 2% 0;
    display: flex;
        flex-direction: column;
        align-items: center;
}

label{
    margin-top: 1%;
    width: 100%;
    color: white;
}

input, textarea{
    width: 100%;
    border-radius: 6px;
    margin-top: 1%;
    color: white;
    background-color: rgba(128, 0, 128,.8);
}

input{
    height: 50px; 
}

textarea{
    height: 100px;
}

.contacto{
    padding: 2% 0;
    display: flex;
        flex-direction: column;
        align-items: center;
    background-image: linear-gradient(150deg,#9500ff,#09f,#0df);
}

.redes{
    width: 60%;
    display: flex;
        justify-content: center;
    
}

.red{
    margin: 1% 2%;
    opacity: 0.5;
    transition: .5s;
    cursor:pointer;
}

.red:hover{
    opacity: 1;
}

.red:active{
    transform: scale(0.8);
}

@media only screen and (min-width:0px) and (max-width:920px){
    #formulario{
        padding: 2% 0 30% 0;
    }

    .contacto h1{
        padding: 0 0 20% 0;
    }
  }